.wrapper{
  width: 100%;
  height: 100%;
  max-width: 1280px;
  padding: 0px 25px;
  margin:auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  font-size: 14px;
  font-weight: 300;
  padding-bottom: 130px;
}

.title{
  font-size: 36px;
  font-weight: 700;
}

.rating{
  font-size: 16px;
  margin-bottom: 10px;
  font-weight: 300;
}

.accent{
  font-weight: 700;
  color: #FF941C;;
}

.overview{
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: flex-end;
}

@media screen and (min-width: 480px) {
  .overview{
    width: 50%;
  }  
}

.trending-section {
  width: 100%;
  max-width: 1280px;
  margin: auto;
  transform: translateY(-50px);
}

.list{
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}

.item{
  flex-basis: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

@media screen and (min-width: 480px) {
  .item {
    flex-basis: calc((100% - 40px) / 2)
  }
}

@media screen and (min-width: 768px) {
  .item {flex-basis: calc((100% - 120px) / 4)}
}

@media screen and (min-width: 1200px) {
  .item {
    flex-basis: calc((100% - 160px) / 5)
  }
}

.item:hover,
.item:focus{
  transform: scale(1.1);
}

.image-cover{
  width: 100%;
  border-radius: 15px;
  overflow: hidden;
}

